













import SchemaForm from '@/schema-form';
import {EffectsContext} from 'v-schema-form-types';
import Vue from 'vue';
import Component from 'vue-class-component';

@Component({
  name: 'SetValue'
})
export default class SetValue extends Vue {
  public schema = {
    fields: {
      name: {
        type: 'string',
        title: '名称'
      }
    }
  };
  public value = {
    name: 'abc'
  };

  public created() {
    SchemaForm.registerElement();
  }

  public setValue() {
    this.value = {
      name: '直接对value赋值'
    };
  }

  public setValue2() {
    this.value.name = '只改变value的name属性';
  }

  public effects($: EffectsContext) {
    $(this.schema.fields.name).onFieldCreateOrChange(() => {

    });
  }
}
